import timezones from '../models/timezones';

const SettingsBrandingStore = {
  state: {
    settingsData: {
      date_format: "",
      time_format: "",
      time_zone: "",
      enable_dst: false,
      enable_support: false,
      support_url: "",
      branding_favicon: "",
      branding_logo: "",
      branding_name: "",
      branding_url: "",
      welcome_heading: "",
      welcome_subheading: ""
    },
    fetchingSettingsData: true,
    timezoneOptions: timezones
  },
  mutations: {
    setSettingsAndBranding(state, data) {
      let obj = data[0];
      for (const key in obj) {
        if (obj[key]) {
          if (key === "branding_logo" || key === "branding_favicon") {
            state.settingsData[key] = obj[key]['original']
          } else {
            state.settingsData[key] = obj[key]
          }
        } else {
          state.settingsData[key] = obj[key]
        }
      }
      state.fetchingSettingsData = false;
    },
    setDefaults(state){
      state.settingsData.date_format = "YYYY-MM-DD";
      state.settingsData.time_format = "h:mm A";
      state.settingsData.time_zone = 0;
      state.fetchingSettingsData = false;
    }
  },
  actions: {
    checkSettingsAndBrandingFields(context, settingsData) {
      let data;
      if(settingsData.results.length) {
        data = settingsData.results[0];
      } else { 
        context.commit("setDefaults"); 
        data = context.state.settingsData; 
      }
      context.commit("setSettingsAndBranding", data);
      localStorage.setItem("settingsData", JSON.stringify(data));
    }
  }
}
export default SettingsBrandingStore;