<template>
  <ins-input ref="insInputEl"
    v-bind:label="label"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:name="name"
    v-bind:field="field"
    v-bind:field-id="fieldId"
    v-bind:icon="icon"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:required="required"
    v-bind:maxlength="maxlength"
    v-bind:error-message="errorMessage"
    v-bind:has-error="hasError"
    @valueChange="onValueChange" />
</template>

<script>
import Validators from '../../mixins/Validators';
import InsitesService from '../../mixins/InsitesService';

export default {
  name: "InsInputWrap",
  mixins: [Validators, InsitesService],
  props: {
    label: String,
    value: String,
    placeholder: String,
    name: String,
    field: String,
    fieldId: String,
    secondary: String,
    icon: String,
    errorMessage: String,
    hasError: Boolean,
    disabled: Boolean,
    readonly: Boolean,
    required: String,
    maxlength: String,
    validate: Boolean
  },
  methods: {
    onValueChange(ev) {
      let value = ev.detail;

      if (this.field === "url"){
        value = InsitesUtil.checkURLinput(value, this.secondary);
        this.$refs.insInputEl.value = value;
      }

      if (this.validate) {
        this.checkInput(value);
      }

      this.$emit("input", value);
    },
    checkInput(value){
      if (this.required && !value) {
        return this.toggleError(true, `${this.label} is required.`);
      } else if (value) {
        let error;
        let message;

        switch (this.field) {
          case 'email':
            error = this.validateEmail(value);
            message = error ? 'Invalid email address.' : '';
            break;

          case 'url':
            if (this.secondary !== "text"){
              error = this.validateURL(value);
              message = error ? 'Invalid url.' : '';
            }
            break;
        }

        this.toggleError(error, message);

      } else {
        this.toggleError(false, '');
      }
    },
    toggleError(error, message){
      
      this.$refs.insInputEl.hasError = error ? true : false;
      this.$refs.insInputEl.errorMessage = message;

      this.$emit("fieldError", {
        error: error ? true : false,
        target: this
      });
    }
  }
};

</script>
<style></style>