export default {
  data() {
    return {
      insRendererEl: undefined,
    }
  },
  methods: {   
    capitalize(item){
      return this.checkValue(item).split("_").map((s) => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
    },
    convertToPhysicalPath(path) {
        path = path.split('/');
        path.shift();
        path = path.join('/');

        return path;
    },
    removeArrLastIndex(path) {
      path = path.split('/');
      path.pop();
      path = path.join('/');

      return path;
    },
    convertToFolder(path) {
        return path.replace(/\//g, '|');
    },
    returnFormattedDate(dateTime) {
        console.log("returnFormattedDate", dateTime);
        return InsitesUtil.formatLocalisation(dateTime);
    },
    localhostAssetManagerGetFileData(physical_path, response) {
        let result = [];
        
        for (let counter = 0; counter < response.results.data.items.results.length; counter++) {
            let value = response.results.data.items.results[counter];
            if (value.physical_path === physical_path) {
                result = response.results.data.items.results[counter];
                break;
            }
        }

        return result;
    },
    apiHeader(element = document.getElementsByName('csrf-token')[0].content) {
        var headers = {
            "X-CSRF-Token": "",
            "X-Requested-With": "XMLHttpRequest",
            Accept: "application/json",
            "Content-Type": "application/json"
        }

        if (window.location.hostname !== "localhost") {
            headers["X-CSRF-Token"] = element;
        }
        return headers;
    },  }
}
