<template>
  <div id="insites-settings-branding">
    <router-view/>
  </div>
</template>

<script>

import {
  fetchSettingsAndBrandingDataJSON, 
  fetchSystemFieldsDataJSON } 
from "./services/CoreServices";

export default {
  name: 'app',
  methods: {
    async setSettingsAndBranding() {
      let settingsData = await fetchSettingsAndBrandingDataJSON();
      this.$store.dispatch('checkSettingsAndBrandingFields', settingsData);
    },
  },
  mounted(){
    this.setSettingsAndBranding();
  }
}
</script>
<style src="./App.scss" lang="scss"></style>
