import { each } from "lodash";

export default {

    data() {
        return {
            s3Data: '',
            s3FormData: '',
            parsedS3FormData: '',
            s3Image: '',
            s3UploadURL: '',
            s3AssetURL: '',
            s3FileName: '',
            s3Type: 'image',
        }
    },
    methods: {
        setS3FormData(data) {
            let obj = {
                "key": data.key,
                "success_action_status": data.success_action_status,
                "acl": data.acl,
                "policy": data.policy,
                "x-amz-credential": data['x-amz-credential'],
                "x-amz-algorithm": data['x-amz-algorithm'],
                "x-amz-date": data['x-amz-date'],
                "x-amz-signature": data['x-amz-signature'],
                "x-amz-meta-versions": "{}"
            }

            if (data['Content-Disposition']) {
              obj["Content-Disposition"] = data['Content-Disposition']
            }

            return obj;
        },
        async getS3Credentials (url) {
          return await $.ajax({
                type: "get",
                url
              }).done(function(response) {
                  return response;
              }).error(error => {
                  console.error("Error getting POS S3 credentials.", error);
                  return false;
              });
        },
        // init function
        async getS3AssetURL(url, fieldName, imageFile, fileName, type) {
           this.s3Image = imageFile;
           this.s3FileName = fileName;
           this.s3Type = type ? type : 'image'; // image or file
           this.s3Data = await this.getS3Credentials(url);

           await this.buildS3FormData(fieldName);
           return this.s3AssetURL;
        },
        async buildS3FormData(fieldName) {
            let s3Data = this.s3Data[fieldName][this.s3Type].s3_upload.form_data;

            this.s3UploadURL = this.s3Data[fieldName][this.s3Type].s3_upload.direct_upload_url;

            await this.getImage(s3Data);// for localhost - testing / development
        },
        async getImage(data) {
            let image = this.s3Image
                      ? this.s3Image
                      : "";
            this.s3FormData = this.setS3FormData(data);
            this.parsedS3FormData = this.parseToFormData(this.s3FormData);
            this.appendImagetoS3FormData(image);
            await this.s3SubmitForm(this.parsedS3FormData);
          },
          appendImagetoS3FormData(image) {
            // image
            if (image !== "" || image !== undefined) {
                // let base64ImageContent = image.replace(/^data:image\/(png|jpg|jpeg);base64,/, "");
                let base64ImageContent = image.split(',')[1];
                // let img_type = image.match(/^data:image\/(png|jpg|jpeg);base64,/);
                let img_type = image.split(';')[0].split('/') // file type
                if (img_type) {
                  let blob = this.base64ToBlob(base64ImageContent, "image/" + img_type[1]);
                  this.parsedS3FormData.append("file", blob, this.s3FileName);
                }
            }
          },
          base64ToBlob(base64, mime) {
            mime = mime || "";
            let sliceSize = 1024;
            let byteChars = window.atob(base64);
            let byteArrays = [];

            for (
              let offset = 0, len = byteChars.length; offset < len; offset += sliceSize
            ) {
              let slice = byteChars.slice(offset, offset + sliceSize);

              let byteNumbers = new Array(slice.length);
              for (let i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
              }

              let byteArray = new Uint8Array(byteNumbers);

              byteArrays.push(byteArray);
            }

            return new Blob(byteArrays, {
              type: mime
            });
          },
          parseToFormData(data) {
            let formData = new FormData();

            each(data, (val, key) => {
              formData.append(key, val);
            });
            return formData;
          },
          async s3SubmitForm(data) {
            let self = this;
              return await $.ajax({
                  type: "post",
                  // dataType: "json",
                  url: self.s3UploadURL, // get url from form tag
                  contentType: false, // guess Content-Type header (it has to be multipart/form-data)
                  processData: false, // do not try to process binary data
                  data  // create FormData object and populate it with form element passed into constructor
                }).done(function(response) {
                   let $xml = $(response);
                   let location = $xml.find('Location').text();

                   self.s3AssetURL = location;
                }).error(error => {
                    console.error("Error uploading file to S3.", error);
                });
          },
    }
}
