const axios = require('axios');
const api_url = "/insites/api/";

// return settings & branding data
export function fetchSettingsAndBrandingDataJSON() {
    return new Promise((resolve) => {
        let url = `${api_url}site-settings-data.json`;
        return axios.get(url)
            .then(results => {
                resolve({ state: true, results: results.data.items.results });  
            }).catch(error => {
                // User is not subscribed to Settings & Branding Module.
                console.warn("Note: You are not subscribed to Insites Settings & Branding Module.");
                resolve({ state: false, results: error });
                // toastIt("error", "Failed to fetch settings and branding.", this.ops);
            });
    });
}

// return system fields data
export function fetchSystemFieldsDataJSON(type, page = 1, size = 10) {
    return new Promise((resolve) => {
        let url = `${api_url}get-system-fields-data.json?key=${type}&page=${page}&size=${size}`;
        return axios.get(url)
            .then(results => {
                resolve({ state: true, results: results.data }); 
            }).catch(error => {         
                resolve({ state: false, results: error });
            });
    });
}