<template>
  <ins-textarea
    v-bind:label="label"
    v-bind:value="value"
    v-bind:placeholder="placeholder"
    v-bind:name="name"
    v-bind:disabled="disabled"
    v-bind:readonly="readonly"
    v-bind:required="required"
    v-bind:maxlength="maxlength"
    v-bind:error-message="errorMessage"
    v-bind:has-error="hasError"
    @valueChange="onValueChange" />
</template>

<script>
export default {
  name: "InsTextareaWrap",
  props: {
    label: String,
    value: String,
    placeholder: String,
    name: String,
    errorMessage: String,
    maxlength: String,
    hasError: Boolean,
    disabled: Boolean,
    readonly: Boolean,
    required: String
  },
  methods: {
    onValueChange(ev) {
      this.$emit("input", ev.detail);
    }
  }
};


</script>
<style></style>