<template>
  <ins-image-picker
    v-bind:img-type="imgType"
    v-bind:label="label"
    v-bind:button-color="buttonColor"
    v-bind:value="value"
    v-bind:upload-img-container="uploadImgContainer"
    v-bind:upload-img-rec-width="uploadImgRecWidth"
    v-bind:upload-img-rec-height="uploadImgRecHeight"
     v-bind:upload-img-rec-file-size="uploadImgRecFileSize"
    @valueChange="onValueChange" />
</template>

<script>
export default {
  name: "InsImagePickerWrap",
  props: {
    imgType: String,
    buttonColor: String,
    value: String,
    label: String,
    uploadImgContainer: String,
    uploadImgRecWidth: "",
    uploadImgRecHeight: "",
    uploadImgRecFileSize: ""
  },
  methods: {
    onValueChange(ev) {
      let self = this;
      self.$emit("input", ev.detail.base64);
      self.$emit('fileNameChanged', ev.detail.filename);
    }
  }
 
};

</script>
