import Vue from "vue";
import Router from "vue-router";

// Settings & Branding
import SiteSettings from "./views/SiteSettings/SiteSettings.vue";

Vue.use(Router);

export default new Router({
  routes: [    
    // Settings & Branding Module
    {
      path: "/settings-and-branding",
      name: "SiteSettings",
      component: SiteSettings
    },
  ]
});
