import { validate } from 'validate.js';

export default {
  methods: {
    validateEmail(value){
      let constraints = { from: { email: true } };
      let attribute = { from: value };
      return validate(attribute, constraints);
    },
    validateURL(value){
      let constraints = { website: { url: true } };
      let attribute = { website: value };
      return validate(attribute, constraints);
    }
  }
}