<template>
<div id="siteSettings">
  <div class="grid-container-full" v-if="initialised">
    <ins-card steady>
      <div class="grid-x grid-padding-x">
        <div class="cell large-6 medium-6 small-12">
          <form id="settingsForm" action="#" data-abide data-live-validate="true"
            novalidate onsubmit="event.preventDefault()" enctype="multipart/form-data">
            <div class="grid-x grid-padding-x grid-padding-y">
              <div class="cell large-9 medium-10 small-12">
                <h4 class="ins-card-heading">Localization</h4>
                <InsSelectWrap id="dateFormat" placeholder="- Select -"
                  name="dateFormat" label="Date Format" v-model="storeSettingsData['date_format']"
                  v-on:input="formatCurrentTime">
                  <!-- <ins-select-option value="" label="Please select Date Format" disabled></ins-select-option> -->
                  <ins-select-option value="MM/DD/YYYY" label="MM/DD/YYYY (02/23/2018)"></ins-select-option>
                  <ins-select-option value="DD/MM/YYYY" label="DD/MM/YYYY (23/02/2018)"></ins-select-option>
                  <ins-select-option value="YYYY/MM/DD" label="YYYY/MM/DD (2018/02/23)"></ins-select-option>
                  <ins-select-option value="MM.DD.YYYY" label="MM.DD.YYYY (02.23.2018)"></ins-select-option>
                  <ins-select-option value="DD.MM.YYYY" label="DD.MM.YYYY (23.02.2018)"></ins-select-option>
                  <ins-select-option value="YYYY.MM.DD" label="YYYY.MM.DD (2018.02.23)"></ins-select-option>
                  <ins-select-option value="MM-DD-YYYY" label="MM-DD-YYYY (02-23-2018)"></ins-select-option>
                  <ins-select-option value="DD-MM-YYYY" label="DD-MM-YYYY (23-02-2018)"></ins-select-option>
                  <ins-select-option value="YYYY-MM-DD" label="YYYY-MM-DD (2018-02-23)"></ins-select-option>
                </InsSelectWrap>

                <InsSelectWrap id="timeFormat" name="timeFormat" label="Time Format"
                  v-model="storeSettingsData['time_format']" v-on:input="formatCurrentTime" 
                  placeholder="- Select -">
                  <!-- <ins-select-option value="" label="Please select Time Format" disabled></ins-select-option> -->
                  <ins-select-option value="h:mm A" label="12 Hours (02:00 pm)"></ins-select-option>
                  <ins-select-option value="HH:mm" label="24 Hours (14:00)"></ins-select-option>
                </InsSelectWrap>

                <InsSelectWrap id="timeZone" name="timeZone" label="Timezone" searchable
                  placeholder="- Select -" v-model="storeSettingsData['time_zone']"
                  v-on:input="formatCurrentTime">

                  <ins-select-option
                    v-for="(option, key, index) in timezoneOptions"
                    v-bind:key="index"
                    v-bind:label="option.label"
                    v-bind:value="option.value">
                  </ins-select-option>
                  
                </InsSelectWrap>

                <ins-toggle-switch label="Daylight Savings Time"
                  v-on:onCheckInsToggleSwitch="toggleDST" v-model="storeSettingsData['enable_dst']"
                  v-bind:checked="storeSettingsData['enable_dst']"></ins-toggle-switch>

                <p class="current-time">Current Time: {{ currentTime }}</p>
              </div>
            </div>
            <div class="grid-x grid-padding-x grid-padding-y">
              <div class="cell large-9 medium-10 small-12">
                <h4 class="ins-card-heading">Support Settings</h4>
                <ins-toggle-switch label="Enable Support"
                  v-on:onCheckInsToggleSwitch="toggleSupportStatus" v-model="storeSettingsData['enable_support']"
                  v-bind:checked="storeSettingsData['enable_support']" ></ins-toggle-switch>
                <InsInputWrap name="form[properties_attributes][support_url]"
                  label="Support URL" autocomplete="off"
                  placeholder="https://docs.insites.io" ref="supportURL"
                  errorMessage="Support URL is required." maxlength="50"
                  field="url" v-bind:validate="true"
                  v-model="storeSettingsData['support_url']" v-bind:required="checkValue(storeSettingsData['enable_support']) !== '' ? storeSettingsData['enable_support'].toString(): 'false'"
                  />
              </div>
            </div>

            <div class="grid-x grid-padding-x grid-padding-y">
              <div class="cell large-9 medium-10 small-12">
                <h4 class="ins-card-heading margin-top-l">Email Settings</h4>
                <InsInputWrap id="emailName" name="form[properties_attributes][email_name]"
                  label="Email Sender Name" field="text" autocomplete="off"
                  v-model="storeSettingsData['email_name']" />

                <InsInputWrap id="mailFrom" name="form[properties_attributes][mail_from]"
                  label="Email From" field="text" autocomplete="off" v-model="storeSettingsData['mail_from']"
                  errorMessage="Invalid email address."
                  v-on:keyup.native="validateEmail" />

                <InsInputWrap id="replyTo" name="form[properties_attributes][reply_to]"
                  label="Reply To" field="text" autocomplete="off" v-model="storeSettingsData['reply_to']"
                  errorMessage="Invalid email address."
                  v-on:keyup.native="validateEmail" />
              </div>
            </div>
          </form>
        </div>
        <div class="cell large-6 medium-6 small-12">
          <div class="grid-x grid-padding-x grid-padding-y">
            <div class="cell large-9 medium-12 small-12">
              <h4 class="ins-card-heading">Branding Settings</h4>

              <small><strong>ICON</strong></small>
              <InsImagePickerWrap name="form[custom_images_attributes][branding_favicon][image]"
                  type="icon" button-color="blue" v-model="s3IconImage"
                  v-on:fileNameChanged="getIconName" label="ADD ICON" 
                  upload-img-container="square">
              </InsImagePickerWrap>

              <small><strong>LOGO</strong></small>
              <InsImagePickerWrap name="form[custom_images_attributes][branding_logo][image]"
                  type="logo" button-color="blue" v-model="s3LogoImage"
                  v-on:fileNameChanged="getLogoName" label="ADD LOGO" 
                  upload-img-container="rectangle" 
                  upload-img-rec-width=360 
                  upload-img-rec-file-size=200>
              </InsImagePickerWrap>

              <InsInputWrap name="form[properties_attributes][branding_name]"
                label="Brand Name" field="text"
                autocomplete="off" errorMessage="Name is required."
                maxlength="50" ref="brandingName"
                placeholder="Insites"
                v-model="storeSettingsData['branding_name']" />

              <InsInputWrap name="form[properties_attributes][branding_url]"
                label="Website URL" 
                autocomplete="off" errorMessage="Website URL is required." 
                maxlength="50" ref="brandingURL"
                placeholder="https://insites.io/"
                field="url" v-bind:validate="true"
                v-model="storeSettingsData['branding_url']" />

            </div>
          </div>
          <div class="grid-x grid-padding-x grid-padding-y">
            <div class="cell large-9 medium-12 small-12">
              <h4 class="ins-card-heading margin-top-l">Welcome Screen</h4>
              <InsInputWrap name="form[properties_attributes][welcome_heading]"
                label="Heading" field="text" autocomplete="off" errorMessage="Heading is required."
                maxlength="50" ref="welcomeHeading" v-model="storeSettingsData['welcome_heading']" />

              <InsInputWrap name="form[properties_attributes][welcome_subheading]"
                label="Subheading" field="text" autocomplete="off" errorMessage="Subheading is required."
                maxlength="50" ref="welcomeSubheading" v-model="storeSettingsData['welcome_subheading']" />
            </div>
          </div>
        </div>
      </div>
    </ins-card>
    <div class="grid-x grid-padding-y">
      <div class="cell large-12 medium-12 small-12 text-right">
        <ins-button type="button" label="Update" color="blue" solid="true" size="normal"
          v-on:onClickInsButton="bindValues" v-bind:disabled="submittingForm"></ins-button>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import moment from "moment";
import { isEmpty } from "lodash";
import { mapState } from "vuex";
import { each } from "lodash";

import InsInputWrap from "../../components/InsInputWrap/InsInputWrap.vue";
import InsSelectWrap from "../../components/InsSelectWrap/InsSelectWrap.vue";
import InsImagePickerWrap from "../../components/InsImagePickerWrap/InsImagePickerWrap.vue";
import InsTextareaWrap from "../../components/InsTextareaWrap/InsTextareaWrap.vue";

import InsitesService from '../../mixins/InsitesService';
import S3UploaderService from '../../mixins/S3UploaderService';

export default {
  name: "SiteSettings",
  mixins: [InsitesService, S3UploaderService],
  components: {
    InsInputWrap,
    InsSelectWrap,
    InsImagePickerWrap,
    InsTextareaWrap
  },
  computed: mapState({
    fetchingSettingsData: state => state.SettingsBrandingStore.fetchingSettingsData,
    timezoneOptions: state => state.SettingsBrandingStore.timezoneOptions,
  }),
  watch: {
    fetchingSettingsData(newState, oldState){
      if (!newState){
        this.initialisePage();
      }
    }
  },
  data() {
    return {
      currentTime: '',
      submittingForm: false,
      newSettings: false,
      headers: {},
      IconName: "",
      LogoName: "",
      authToken: "",
      showModal: false,
      formType: "PUT",
      sourceData: "/insites/api/site-settings-data.json",
      logoName: "",
      formURL: "/api/customizations",
      formData: {},
      parsedFormData: {},
      initialised: false,
      storeSettingsData: {},

      // s3
      s3FormCredentialURL: "/insites/api/s3-settings-branding-form.json",
      s3IconChange: false, // flag if changed
      s3LogoChange: false, // flag if changed
      s3IconImage: '', // icon image
      s3IconURL: '', // submit URL
      s3LogoImage: '', // logo image
      s3LogoURL: '', // submit URL
      s3Data: "", // raw s3 formdata
      s3FormData: "", // s3 formdata + appended attachments
      parsedS3FormData: "" // parsed data to be submitted
    };
  },
  methods: {
    checkURL(url){
        if (url.includes('https://')){
            return url;
        } else if (url.includes('http://')){
            return url;
        } else {
            return `//${url}`;
        }
    },
    initialisePage(){
      this.storeSettingsData = JSON.parse(localStorage.getItem("settingsData"));
      this.formatCurrentTime();

      this.s3IconImage = this.storeSettingsData.branding_favicon.original;
      this.s3LogoImage = this.storeSettingsData.branding_logo.original;
    },
    toggleSupportStatus(event) {
      this.storeSettingsData['enable_support'] = event.detail.checked;
      this.storeSettingsData['enable_support'] ? this.validateInput(this.$refs.supportURL.$el) : this.validFieldNotif(this.$refs.supportURL.$el);
    },
    toggleDST(event) {
      this.storeSettingsData['enable_dst'] = event.detail.checked;
      this.formatCurrentTime();
    },
    getIconName(event) {
      this.IconName = event;
      this.s3IconChange = true;
    },
    getLogoName(event) {
      this.LogoName = event;
      this.s3LogoChange = true;
    },
    async buildFormData() {
      this.formData = {
        "authenticity_token": this.authToken,
        "contentType": false,
        "processData": false,
        "form_configuration_name": "modules/insites_settings_and_branding/insites_settings_branding",
        "parent_resource_id": "modules/insites_settings_and_branding/insites_site_settings",
        "form[properties_attributes][date_format]": this.checkValue(this.storeSettingsData['date_format']),
        "form[properties_attributes][time_format]": this.checkValue(this.storeSettingsData['time_format']),
        "form[properties_attributes][time_zone]": this.checkValue(this.storeSettingsData['time_zone']),
        "form[properties_attributes][enable_dst]": this.storeSettingsData['enable_dst'],
        "form[properties_attributes][enable_support]": this.storeSettingsData['enable_support'],
        "form[properties_attributes][support_url]": this.checkValue(this.storeSettingsData['support_url']) === '' ? '' : this.storeSettingsData['support_url'],
        "form[properties_attributes][branding_name]": this.checkValue(this.storeSettingsData['branding_name']),
        "form[properties_attributes][branding_url]": this.checkValue(this.storeSettingsData['branding_url']) === '' ? '' : this.storeSettingsData['branding_url'],
        "form[properties_attributes][email_name]": this.checkValue(this.storeSettingsData['email_name']),
        "form[properties_attributes][mail_from]": this.checkValue(this.storeSettingsData['mail_from']),
        "form[properties_attributes][reply_to]": this.checkValue(this.storeSettingsData['reply_to']),
        "form[properties_attributes][welcome_heading]": this.checkValue(this.storeSettingsData['welcome_heading']),
        "form[properties_attributes][welcome_subheading]": this.checkValue(this.storeSettingsData['welcome_subheading'])
      }
      this.parsedFormData = this.appendToFormData(this.formData);  
    },
    async bindValues() {
      this.submittingForm = true;
      this.buildFormData();

      // td
      // favicon
      if (this.s3IconChange) {    
        this.s3IconImage = await this.getS3AssetURL(this.s3FormCredentialURL,'branding_favicon', this.s3IconImage ,this.IconName);
        this.parsedFormData.append("form[custom_images_attributes][branding_favicon_attributes][image]", this.s3IconImage);
        this.s3IconChange = false;
      }

      // logo
      if (this.s3LogoChange) {   
        this.s3LogoImage  = await this.getS3AssetURL(this.s3FormCredentialURL,'branding_logo', this.s3LogoImage ,this.LogoName);
        this.parsedFormData.append("form[custom_images_attributes][branding_logo_attributes][image]", this.s3LogoImage);
        this.s3LogoChange = false;
      }

      let form = document.getElementById("settingsForm");
      if (InsitesUtil.formValidate(Array.from($(form).find((".ins-form-field, .ins-select-value-input, .ins-textarea-field"))))) {
        this.setAjaxForm(this.parsedFormData);
      } else {
        this.addMore = false;
        this.submittingForm = false;
      }
    },
    async setAjaxForm(data) {
      await this.getSettingsData();
      if (this.newSettings && isEmpty(this.storeSettingsData.length)) {
        this.formURL = `/api/customizations`;
        this.formType = "post";
      } else {
        this.formURL = `/api/customizations/${this.storeSettingsData.id}`;
        this.formType = "put";
      }
      this.submitForm(data);
    },
    async getSettingsData() {
      let self = this;
      await this.$http
        .get(this.sourceData)
        .then(response => {
          if (response.data.items.results.length) {
            self.newSettings = false;
          } else {
            self.newSettings = true;
          }
        })
        .catch(error => {
          console.error("Error: " + error);
          this.errorHandler();
          return [];
        });
    },
    async refreshSettingsData() {
      let settingsData = await fetchSettingsAndBrandingDataJSON();
      this.$store.dispatch('checkSettingsAndBrandingFields', settingsData);
    },
    submitForm(data) {
      let self = this;

      this.$http[this.formType](this.formURL, data, {
          headers: this.headers
        })
        .then(res => {
          this.submittingForm = false;
          parent.toastIt("success", "Settings and Brandings has been updated. Please refresh the page to see the changes.", this.ops);
          this.refreshSettingsData();
        }).catch(error => {
          this.submittingForm = false;
          console.error('error', error);
          parent.toastIt("error", "Failed to update settings.", this.ops);
        });
    },
    base64ToBlob(base64, mime) {
      mime = mime || "";
      let sliceSize = 1024;
      let byteChars = window.atob(base64);
      let byteArrays = [];

      for (
        let offset = 0, len = byteChars.length; offset < len; offset += sliceSize
      ) {
        let slice = byteChars.slice(offset, offset + sliceSize);

        let byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
          byteNumbers[i] = slice.charCodeAt(i);
        }

        let byteArray = new Uint8Array(byteNumbers);

        byteArrays.push(byteArray);
      }

      return new Blob(byteArrays, {
        type: mime
      });
    },
    appendToFormData(data) {
      let formData = new FormData();

      each(data, (val, key) => {
        formData.append(key, val);
      });
      return formData;
    },
    checkValue(value, withPlus) {
      return value && value !== 'undefined' && value !== null ? `${withPlus ? '+':''}${value}` : '';
    },    
    formatCurrentTime() {
      let dateFormat = 'YYYY-MM-DD';
      let timeFormat = 'h:mm A';
      let timezoneVal = 0;
      let settingsData = JSON.parse(localStorage.getItem('settingsData'));
      var dateTimeFormat = `${dateFormat} ${timeFormat}`; 

      if(settingsData) {
          dateFormat = settingsData.date_format ? settingsData.date_format : 'YYYY-MM-DD';
          timeFormat = settingsData.time_format ? settingsData.time_format : 'h:mm A';                
          dateTimeFormat = `${dateFormat} ${timeFormat}`;
          timezoneVal = 0;

          if (settingsData.time_zone){
              timezoneVal = (parseInt(settingsData.time_zone.split('@')[0])) / 100;
          }

          if (settingsData['enable_dst']) {
              timezoneVal += 1;
          }
      }

      this.currentTime = moment().utcOffset(timezoneVal).format(dateTimeFormat);
      this.initialised = true;
    }
  },
  mounted() {
    if (!this.fetchingSettingsData){
      this.initialisePage();
    }
  },
  created() {
    this.headers = this.apiHeader();
    this.authToken = this.headers["X-CSRF-Token"];
  }
};
</script>
<style lang="scss" scoped src="./SiteSettings.scss">
</style>